body {
	margin: 0;
	padding: 0;
	font-family: "proxima-nova", sans-serif;
}
.fade-enter {
	opacity: 0;
	z-index: 2;
}
.fade-leave {
	opacity: 0;
	z-index: 1;
}

.fade-enter.fade-enter-active {
	opacity: 1;
	-webkit-transition: opacity 300ms ease-in;
	-o-transition: opacity 300ms ease-in;
	transition: opacity 300ms ease-in;
}



